html{
    margin:0;
    padding: 0;
    height: 100%;
}
body{
    padding: 0;
    margin: 0 0 0 0;
    height: 100%;

}
#root{
    height: 100%;
}
.container {
    background: rgb(202, 238, 204);
    font-size:2rem;
    grid-row: 1;
    grid-column: 2;
    padding: 2rem;
    border: teal solid 2px;
    margin: 2rem auto;
    display: grid;
    grid-template-columns:  1fr 1fr 1fr 1fr;
    grid-template-rows:  repeat(8,1fr) ;
    gap: 0.5rem;
    justify-content:center ;
    justify-self: center;
    max-width: 560px;
    min-width: 375;
    min-height:85vh;
}

.display{
    border: solid 2px black;
    grid-column:1 / span 4;
    grid-row:  1 /span 3;
    background:white;
}

#seven{
grid-column:1;
grid-row: 5;
}

#eight {
    grid-column:2;
    grid-row: 5;
}


#nine{
    grid-column:3;
    grid-row: 5;
}

#four{
    grid-column:1;
    grid-row: 6;
}
#five{
    grid-column:2;
    grid-row: 6;
}
#six{
    grid-column:3;
    grid-row: 6;
}
#one{
    grid-column:1;
    grid-row: 7;
}
#two{
    grid-column:2;
    grid-row: 7;
}
#three {
    grid-column:3;
    grid-row: 7;
}
#zero {
    grid-column: 1 / span 2;
    grid-row: 8;
}
#decimal{
    grid-column:3;
    grid-row: 8;
}

#equal{
    grid-column:4;
    grid-row: 8;
}
#add {
    grid-column:4;
    grid-row: 7;
}

#subtract{
    grid-column:4;
    grid-row: 6;
}
#multiply{
    grid-column:4;
    grid-row: 5;
}
#divide {
    grid-column:4;
    grid-row: 4;
}
#clear {
    grid-column:1 / span 3;
    grid-row: 4;
}